<template>
  <div class="flastcon">
	  <div style="position: relative;">
			<div class="yuanIN" v-show="carmud!=0">{{carmud}}</div>
		 <img @click="incar()" class="imgbox" :src="carimg" alt=""> 
	  </div>
      <img @click="GZHPopup = true" class="imgbox imgboxdd" :src="shareimg" alt="">
	  <!-- <img @click="JGRZHU()" class="imgbox" :src="shareimg" alt=""> -->
	  <!-- 遮罩 -->
	  <div v-show="sharebgstate" class="sharebg" @click="sharebg()">
		  <img style="float: right;margin-top: 20px;" src="../../assets/jt.png" alt="">
	  </div>
	  <!-- 机构入住 -->
	  <van-action-sheet v-model="JGshow" title="机构加盟">
			<div style="padding: 24px 40px 24px 24px;">
				<van-field v-model="JGname" label="机构名称" left-icon="home-o" placeholder="请输入" />
				<van-field v-model="INname" label="联系人" left-icon="contact" placeholder="请输入" />
				<van-field v-model="INtel" label="手机号码" left-icon="phone-o" placeholder="请输入" />
				<van-button type="primary" color="#fe892b" size="large" class="butINJG" @click="INJGbutton()">立即加盟</van-button>
			</div>		  
	  </van-action-sheet>
	  <!-- 公众号弹出窗 -->
	  <div class="GZHPopupTC" v-show="GZHPopup" @click="GZHPopup = false"></div>
	  <div class="GZHPopupCON" v-show="GZHPopup">
	  	<img class="GZHPopupCONcode" v-lazy="imgfx"  alt="">
	  	<div class="GZHPopupCONtit">长按保存图片到相册</div>
	  </div>
  </div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
	  data() {
	    return {
			sharebgstate:false,
			carmud:'0',
			JGshow:false,
			JGname:'',
			INname:'',
			INtel:'',
			carimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/cart.png',//购物车logo
			shareimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/share.png',//分享logo
			GZHPopup:false,
			imgfx:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/fx.png'
	    };
	  },
	  props:['carlenght'],
	  watch:{
		  carlenght(){
			 this.carajaxlist(); 
		  },
	  },
	  methods: {
			incar(){
				this.$router.push({path:'/car'});
			},
			fenxiang(){
				// window.location.href = 'https://yxhd.insooner.com/dista?urluid='+localStorage.getItem('INContrariantoken');
				this.sharebgstate=true;
			},
			sharebg(){
				this.sharebgstate=false;
			},
			//购物车
			carajaxlist(){
				let dataS={
					user_id:localStorage.getItem('INContrariantoken')
				};
				this.axios.post('v1/getContrarianCatCourse',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					if(response.data.code == 1000){
						this.carmud = response.data.data.length;
					};
				})
			},
			//机构入住
			JGRZHU(){
				this.JGshow=true;
			},
			// 提交
			INJGbutton(){
				if(!(/^1[3456789]\d{9}$/.test(this.INtel))){
					Toast("手机号码有误，请重填");  
					return false; 
				}else{
					let dataJGOobj={
						name:this.INname,
						cname:this.JGname,
						phone:this.INtel,
						uid:localStorage.getItem('INContrariantoken')
					};
					this.axios.post('addClient',this.qs.stringify(dataJGOobj)).then((response)=>{
						// ...
						console.log('机构入驻提交',response.data.data);
						this.JGshow=false;
						Toast("感谢您对因学的信赖，我们将在2个工作日内与您联系，请您耐心等待！\n\n如果您迫切着急：请您拨打4000060996与我们联系。");
					})
				}
			}
	  },
	  mounted() {
		  this.carajaxlist()
	  }
	};
</script>

<style scoped>
	.yuanIN{
		position: absolute;
		top: 0;
		right: 4px; 
		width: 20px;
		height: 20px;
		border-radius: 50%;
		background-color: #ee0a24;
		line-height: 20px;
		text-align: center;
		font-size: 12px;
		color: #fff;
	}
  .flastcon{
    position:fixed;
    bottom:120px;
    right: 24px;
    width: 54px;
    z-index:999;
  }
  .imgbox{
    width: 54px;
    height: 54px;
  }
  .sharebg{
	  background-color: rgba(0,0,0,0.5);
	  width: 100%;
	  height: 100%;
	  position: fixed;
	  top: 0;
	  left: 0;
	  z-index: 99;
  }
  .imgboxdd{
  	  -webkit-animation: seconddiv 2s infinite linear;
  	  background: transparent;	
  }
  
  @keyframes seconddiv{
	  0% {transform: scale(1,1);}
	  10% {transform: scale(1,1);}
	  25% {transform: scale(1.2,1.2);}
	  50% {transform: scale(1,1);}
	  70% {transform: scale(1.2,1.2);}
	  100% {transform: scale(1,1);}
  }
  /* 提交按钮 */
  .butINJG{
	  margin: 40px 0 20px 0;
	  
  }
  .GZHPopupTC{
  	position: fixed;
  	width: 100%;
  	height: 100%;
  	background-color: rgba(0,0,0,0.5);
  	z-index: 99;
  	top: 0;
  	left: 0;
  }
  .GZHPopupCON{
  	position: fixed;
  	background-color: #fff;
  	border-radius: 12px;
	width: 70%;
  	top:10%;
  	left: 15%;
  	z-index: 100;
	overflow: hidden;
  }
  .GZHPopupCONcode{
  	width:100%;
  	display: block;
  }
  .GZHPopupCONtit{
  	font-size: 16px;
  	color: #000;
	line-height: 40px;
  }
  .GZHPopupCONtext{
  	font-size: 12px;
  	color: #999999;
  	line-height: 40px;
  }
</style>



