<template>
	<div class="user">
		<div class="usercon">
			<div class="userconlogo">
				<img v-lazy="usercon.img">
			</div>
			<div class="userconname">{{usercon.name}}</div>
			<div class="usercontext" @click="usertext()">编辑</div>
		</div>
		<!-- 内容 -->
		  <van-row class="usernav">
			<van-col span="8" class="usernav_st">
				<div>{{usercon.integral}}</div>
				<p>积分</p>
			</van-col>
			<van-col span="8" class="usernav_st">
				<div>{{usercon.cou_num}}</div>
				<p>课程</p>
			</van-col>
			<van-col span="8" class="usernav_st">
				<div>{{usercon.age}}</div>
				<p>年龄</p>
			</van-col>
		  </van-row>	
		<!-- banner -->
		<img style="width: 100%;display:block;" v-lazy="bannerimg" @click="toindexurl()">
		<!-- 我报名的课程 -->
		<div class="briefJJ">
			<div class="detnav">已报课程</div>
			<div class="briefJJimg" v-for="(item,index) in orderlist" :key="index" >
				<van-card :thumb="item.img" @click-thumb="detJG(item.cid)">
				  <div slot="tags" style="position: relative;">
					<div class="userrem" @click="remuser(item.id)">删除</div>
				    <div class="briefJJtit">{{item.name}}</div>
					<div class="briefJJtitru" style="color: #999999;">{{item.title}}</div>
				  </div>
				</van-card>
			</div>
		</div>
		<!-- 弹窗 -->
		<van-popup v-model="Popupshow" round>
			<div class="Popupshowcon">
			   <h5>我的信息</h5>
			   <van-field v-model="Popuptext.name" label="姓名" placeholder="请输入姓名"/>
			   <van-field v-model="Popuptext.age" type="digit" label="年龄" placeholder="请输入年龄"/>
			   <van-field v-model="Popuptext.phone" type="tel" label="手机号" placeholder="请输入手机号"/>
			   <div style="font-size: 12px;margin:15px 0;">您的信息将会来报名课程中用到，请仔细填写</div>
			   <van-button size="large" type="primary" block color="#c85e55" @click="baomincli()">确认</van-button>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import { Dialog,Toast } from 'vant';
	export default {
	  data() {
	    return {
	      bannerimg:'https://yinxuejiaoyu.oss-cn-hangzhou.aliyuncs.com/images/Manual/Contrarian/yy.png',
		  usercon:{},//用户信息
		  orderlist:[],//订单数据
		  Popupshow:false,
		  // 验证信息
		  Popuptext:{},
	    };
	  },
	  methods:{
		  usertext(){
			  this.Popupshow = true;
			  this.baominclitexdt();
		  },
		  //报名加用户信息修改
		  baomincli(){
		  	if (!(/^1[3456789]\d{9}$/.test(this.Popuptext.phone))) {
		  		return Toast("请手机号填写有误!");  
		  	};
		  	//验证是否有信息
		      let dataS={
		  		user_id:localStorage.getItem('INContrariantoken'),
		  		name :this.Popuptext.name,
		  		age :this.Popuptext.age,
		  		phone :this.Popuptext.phone,
				id:this.Popuptext.id,
		  	};
		  	this.axios.post('v1/upContrarianStudent',this.qs.stringify(dataS)).then((response)=>{
		  	    // ...
		  		Toast(response.data.msg);
		  		if(response.data.code == 1000){
		  			this.Popupshow = false;
		  		};
		  	})
		  },
		  //信息赋值
		  baominclitexdt(){
		  	//验证是否有信息
		    let dataS={
		  		user_id:localStorage.getItem('INContrariantoken'),
		  	};
		  	this.axios.post('v1/getContrarianStudent',this.qs.stringify(dataS)).then((response)=>{
		  	    // ...
		  		if(response.data.code == 1000){
		  			this.Popuptext = response.data.data;
		  		};
		  	})
		  },
		 detJG(e){
			//进入详情
			this.$router.push({path:'/briefcon',query:{id:e}});
		 },
		 remuser(e){
			Dialog.confirm({
			  title: '删除课程',
			  message: '是否删除当前选择的课程'
			}).then(() => {
			  // on confirm
			  let that= this;
			  let thisdata = {
			  	user_id:localStorage.getItem('INContrariantoken'),
				order_id:e
			  };
			  that.axios.post('v1/delContrarianOrder',that.qs.stringify(thisdata)).then((response)=>{
			      // ...		
			  	console.log('删除',response.data);
			  	if(response.data.code ==1000){
			  		Toast(response.data.msg); 
					that._orderLIST();
			  	};
			  })
			}).catch(() => {
			  // on cancel
			});  
		 },
		 // 获取用户信息
		 _conlist(){
			 let that= this;
			 let thisdata = {
			 	user_id:localStorage.getItem('INContrariantoken'),
			 };
			 this.axios.post('v1/getContrarianUserInfo',this.qs.stringify(thisdata)).then((response)=>{
			     // ...		
			 	console.log('用户信息',response.data);
			 	if(response.data.code ==1000){
			 		that.usercon = response.data.data;
			 	};
			 })
		 },
		 // 订单接口
		 _orderLIST(){
			 this.orderlist =  [];
			 let that= this;
			 let thisdata = {
			 	user_id:localStorage.getItem('INContrariantoken'),
			 };
			 this.axios.post('v1/getContrarianOrder ',this.qs.stringify(thisdata)).then((response)=>{
			     // ...		
			 	console.log('用户订单',response.data);
			 	if(response.data.code ==1000){
			 		this.orderlist = response.data.data;
			 	};
			 })
		 },
		 // 跳转到首页
		 toindexurl(){
			this.$router.push({path:'/'});
		 }
	  },
	  mounted() {
	  	this._conlist();
		this._orderLIST();
	  }
	};
</script>

<style scoped>
	/* 弹窗 */
	.Popupshowcon{
		width:280px;
		background-color: #F7F7F7;
		border-radius: 5px;
		overflow: hidden;
	}
	.userrem{
		position: absolute;
		right: 0;
		top: 0;
		font-size: 14px;
		color: #CCCCCC;
		line-height: 27px;
	}
	/* 列表 */
	/* 简介 */
	.briefJJimg{
		text-align: left;
		margin-bottom: 20px;
	}
	.briefJJtitru{
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden;
		font-size: 14px;
	}
	.briefJJtit{
		width: calc(100% - 50px);
		line-height: 27px;
		height: 27px;
		overflow: hidden;
		display: block;
		text-overflow: ellipsis;
		white-space: nowrap;
		font-size: 14px;
	}
	.briefJJ{
		padding: 20px 14px;
		background-color: #fff;
		margin-bottom: 10px;
		text-align: left;
	}
	.briefJJTit{
		height: 44px;
		text-align: left;
		line-height: 44px;
		color:rgba(51,51,51,1);
		font-weight: bold;
	}
	.briefJJTit img{
		width: 22px;
		height: 22px;
		float: left;
		margin: 11px 10px 0 0;
	}
	.briefJJTEXT{
		font-size: 14px;
		color: #736E6B;
		text-align: left;
		margin-bottom: 60px;
		padding-bottom:20px;
	}
	.detnav{
		font-size:16px;
		font-weight: bold;
		text-align: left;
		position: relative;
		padding-left:12px;
		color: #333333;
		margin-bottom:20px;
	}
	.detnav::after{
		position: absolute;
		top: 0;
		left: 0;
		width: 4px;
		height: 21px;
		content: '';
		background:linear-gradient(313deg,#e08078 0%,#c85e55 100%);
		border-radius:10px;
	}
	.detnavimg{
		width: 100%;
		max-height:180px;
		overflow: hidden;
		border-radius: 12px;
		margin-bottom: 12px;
	}
	.detnavimg img{
		width: 100%;
		display: block;
	}
	/* 上半部分 */
	.usernav{
		margin: 15px 0;
	}
	.usernav_st div{
		font-size: 16px;
		color: #333333;
	}
	.usernav_st p{
		margin: 6px 0;
		color: #999;
		font-size: 14px;
	}
	.user{
		width:calc(100% - 30px);
		height:calc(100% - 30px);
		padding:15px;
	}
	.usercon{
		width: 100%;
		overflow: hidden;
	}
	.userconlogo{
		float: left;
		width: 60px;
		height: 60px;
		margin-right: 15px;
		border-radius: 50%;
		overflow: hidden;
	}
	.userconlogo img{
		width: 100%;
		display: block;
	}
	.userconname{
		font-size:16px;
		color: #333333;
		width: calc(100% - 150px);
		float: left;
		line-height: 60px;
		text-align: left;
	}
	.usercontext{
		float: right;
		border: 1px #c85e55 solid;
		font-size: 12px;
		color: #c85e55;
		padding: 2px 12px;
		border-radius: 12px;
		margin-top: 19px;
	}
</style>



