<template>
	<div class="incar">
		<div class="incarBox">
			<van-checkbox-group v-model="result" v-for="(item, index) in list" :key="index">
				<div class="incarBoxklist">
					<div class="incarBoxklistlift">
						<van-checkbox checked-color="#c85e55" class="incarBoxklistliftcheckbox" :key="item.id" :name="item"></van-checkbox>
					</div>
					<div class="incarBoxklistZ">
						<img :src="item.img" alt="">
						<div class="incarBoxklistZCOn">
							<p style="font-size:14px;line-height:30px;height: 30px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;">{{item.name}}</p>
							<p style="margin-bottom: 16px;"><span class="incarBoxklistZCOnvac">{{item.ccname}}</span></p>
						</div>
					</div>
					<div class="incarBoxklistright">
						<img src="../../assets/delet.png" alt="">
						<span @click="delbox(item.id)">删除</span>
					</div>
				</div>
			</van-checkbox-group>
			<div class="wuobjkt" v-show="list.length==0"><span>暂无课程订单</span></div>
		</div>
		<!-- 提交 -->
		<div class="incarbuttom">
			<div class="incarbuttomcheckbox"><span>已选{{this.result.length}}项</span></div>
			<div class="incarbuttombut" @click="boxOK()">确认报名</div>
			<div class="incarbuttomdel" @click="delboxs()">删除</div>
		</div>
		<!-- 弹窗 -->
		<van-popup v-model="Popupshow" round>
			<div class="Popupshowcon">
			   <h5>我要报名</h5>
			   <van-field v-model="Popuptext.name" label="姓名" placeholder="请输入姓名"/>
			   <van-field v-model="Popuptext.age" type="digit" label="年龄" placeholder="请输入年龄"/>
			   <van-field v-model="Popuptext.phone" type="tel" label="手机号" placeholder="请输入手机号"/>
			   <div style="font-size: 12px;margin:15px 0;">我们通过电话与您联系，请保证电话真实畅通~</div>
			   <van-button size="large" type="primary" block color="#c85e55" @click="baomincli()">确认报名</van-button>
			</div>
		</van-popup>
	</div>
</template>

<script>
	import {
		Toast,Dialog 
	} from 'vant';
	export default {
		data() {
			return {
				Popupshow:false,
				// 验证信息
				Popuptext:{
					name:'',
					age:'',
					phone:''
				},
				list: [],//列表内容
				result: [],
				TJlist:[],//报名数组
			};
		},
		methods: {
			//报名加用户信息
			baomincli(){
				if (!(/^1[3456789]\d{9}$/.test(this.Popuptext.phone))) {
					return Toast("请手机号填写有误!");  
				};
				let ides = [];
				this.result.forEach(item =>{
				    ides.push(item.id);
				});
				//验证是否有信息
				 let dataS={
					user_id:localStorage.getItem('INContrariantoken'),
					name :this.Popuptext.name,
					age :this.Popuptext.age,
					phone :this.Popuptext.phone,
					cat_id:ides,
					Pass_order_status:2,
					student_type:1
				 };
				this.axios.post('v1/setContrarianAddOrder',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					Toast(response.data.msg);
					if(response.data.code == 1000){
						this.Popupshow = false;
					};
				})
			},
			//单个删除
			delbox(e) {
				// console.log(e);
				Dialog.confirm({
				  title: '是否真的需要删除！',
				  // message: '弹窗内容'
				}).then(() => {
				  // on confirm
				  let ss = [];
				  ss.push(e);
				  this.carlistdel(ss);
				}).catch(() => {
				  // on cancel
				});
			},
			//全部选择删除
			delboxs() {
				console.log(this.result);
				if (this.result=='') {
					return Toast("您当前还没有选择课程！")
				};
				Dialog.confirm({
				  title: '是否真的需要删除！',
				  // message: '弹窗内容'
				}).then(() => {
				  // on confirm
				  let objlist = [];
				  this.result.forEach((val)=>{
					 objlist.push(val.id);
				   });
				  this.carlistdel(objlist);
				}).catch(() => {
				  // on cancel
				});
			},
			//确认报名
			boxOK() {
				// console.log(this.result)；
				if (this.result=='') {
					return Toast("您当前还没有选择报名课程！")
				} else{
					this.orderlist();
				}
			},
			//加入订单
			orderlist(){
				let ides = [];
				this.result.forEach(item =>{
				    ides.push(item.id);
				});
				// console.log(ides);
				let dataS={
					user_id:localStorage.getItem('INContrariantoken'),
					cat_id:ides,
					Pass_order_status:2,
					student_type:2
				};
				this.axios.post('v1/setContrarianAddOrder',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					Toast(response.data.msg);
					if(response.data.code == 1000){
						this.list = [];
						this.result = [];
						this.carajaxlist();
					}else if(response.data.code == 1001){
						//无用户信息
						this.Popupshow = true;
					}
				})
			},
			// 获取购物车数据
			carajaxlist(){
				this.list = [];
				this.result = [];
				let dataS={
					user_id:localStorage.getItem('INContrariantoken')
				};
				this.axios.post('v1/getContrarianCatCourse',this.qs.stringify(dataS)).then((response)=>{
				    // ...
					console.log('购物车',response.data);
					if(response.data.code == 1000){
						this.list = response.data.data;
						this.result = response.data.data;
					};
				})
			},
			// 删除课程
			carlistdel(e){
				console.log(e);
				let dataSdel={
					cat_id:e,
				};
				this.axios.post('v1/delContrarianCatCourse',this.qs.stringify(dataSdel)).then((response)=>{
				    // ...
					Toast(response.data.msg);
					this.result=[];
					this.carajaxlist();
				})
			},
			//订单生成
			//分享用户授权
			shareures(){
				const INUserID = this.$uerstore.store.state.BDuidTHIS;//传入用户id
				const INUserMID = this.$uerstore.store.state.GJmidTHIS;//传入课程id
				if (INUserID==undefined&&INUserMID==undefined) return;
				if (INUserID==undefined || INUserID==null || INUserID=='') {
					let thisdata = {
						id:localStorage.getItem('INContrariantoken'),
						inviteid:'',
						latitude:localStorage.getItem('INlatitude'),
						longitude:localStorage.getItem('INlongitude'),
						mid:INUserMID
					};
					this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
					    // ...
						console.log('分享信息获取',response);
					});
				}else if(INUserMID==undefined || INUserMID==null || INUserMID==''){
					let thisdata = {
						id:localStorage.getItem('INContrariantoken'),
						inviteid:INUserID,
						latitude:localStorage.getItem('INlatitude'),
						longitude:localStorage.getItem('INlongitude'),
						mid:''
					};
					this.axios.post('setDistribution',this.qs.stringify(thisdata)).then((response)=>{
					    // ...
						console.log('分享信息获取',response);
					});	
				}
				
			}
		},
		mounted() {
			this.carajaxlist();
			// this.shareures();
		}
	};
</script>

<style scoped>
	/* 弹窗 */
	.Popupshowcon{
		width:280px;
		background-color: #F7F7F7;
		border-radius: 5px;
		overflow: hidden;
	}
	
	.incar {
		width: 100%;
		height: 100%;
		background: rgba(250, 250, 249, 1);
	}

	.incarBox {
		padding: 10px 14px 64px 14px;
	}

	.incarBoxklist {
		width: 100%;
		border-radius: 10px;
		background-color: #fff;
		height: 108px;
		margin-bottom: 10px;
		position: relative;
	}

	.incarBoxklistlift {
		height: 100%;
		width: 58px;
		float: left;
	}

	.incarBoxklistliftcheckbox {
		float: left;
		margin: 44px 18px;
	}

	/* 内容 */
	.incarBoxklistZ {
		width: 60%;
		margin-top: 12px;
		height: 84px;
		float: left;
	}

	.incarBoxklistZ img {
		width: 84px;
		height: 84px;
		border-radius: 10px;
		float: left;
	}

	.incarBoxklistZCOn {
		float: left;
		width: calc(100% - 94px);
		margin-left: 10px;
	}

	.incarBoxklistZCOn p {
		width: 100%;
		margin: 0;
		text-align: left;
	}

	.incarBoxklistZCOnvac {
		color: #736E6B;
		font-size: 12px;
		background-color: #F7F7F7;
		padding: 0 10px;
		border-radius: 10px;
		line-height: 20px;
	}

	.incarBoxklistZCOnmud {
		font-size: 12px;
		color: #999999;
	}

	.incarBoxklistZCOnmud span {
		color: #c85e55;
	}

	.incarBoxklistright {
		position: absolute;
		right: 10px;
		top: 10px;
	}

	.incarBoxklistright img {
		width: 16px;
		height: 16px;
		display: block;
		float: left;
	}

	.incarBoxklistright span {
		font-size: 12px;
		float: left;
		margin-left: 6px;
		color: #999999;
	}

	.incarbuttom {
		width: 100%;
		height: 54px;
		background-color: #fff;
		position: fixed;
		bottom: 0;
		left: 0;
	}

	/* 底部栏 */
	.incarbuttomcheckbox {
		float: left;
		margin: 18px 14px;
	}

	.incarbuttomcheckbox span {
		color: #999999;
		margin-left: 6px;
	}

	.incarbuttomdel {
		float: right;
		color: #c85e55;
		line-height: 54px;
		font-size: 12px;
	}

	.incarbuttombut {
		float: right;
		width: 134px;
		height: 44px;
		font-size: 16px;
		line-height: 44px;
		margin: 5px 14px;
		background: linear-gradient(90deg, #c85e55 0%, #e08078 100%);
		color: #fff;
		border-radius: 44px;
	}
	.wuobjkt{
		margin-top:80%;
	}
	.wuobjkt span{
	   border: 1px #999 solid;
	   padding: 6px 20px;
	   color: #999999;
	   border-radius:50px;
	}
</style>



